@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard{
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: #f8f9fa;
    background-image: linear-gradient(to left, #f8f9fa 0%, #dee2e6 100%);

    &__header{
        background-color: v.$red-campus;
        padding: 4rem;
        min-height: 70px;
        background-image: url(../img/header_logo.jpg);
        background-repeat: no-repeat;
        background-size: 300px, 200px, cover, cover;
        background-position: left,center;
    }

    &__header--grid{
        display: flex;
        flex-direction: column;
        @include m.tablet{
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__logo{
        @include m.logo;
        @include m.texto-fluido(2rem, 3rem)
    }

    &__nav{
        
    }

    &__form{
        display: flex;
        justify-content: center;
    }

    &__submit--logout{
        @include m.enlace;
    }

    &__grid{
        display: flex;
        flex: 1;
        overflow-y: auto;
    }

    &__sidebar{
        background-color: lighten(v.$gris-campus, 10);
        width: 12rem;
    }

    &__menu{
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }

    &__enlace{
        color: lighten(v.$gris-campus, 40);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;

        &:hover,
        &--actual{
            background-color: lighten(v.$gris-campus, 20);
            color: v.$blanco;
            border-radius: 1rem;
        }
    }

    &__icono{
        font-size: 2rem;
    }

    &__menu-texto{
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        margin-top: .5rem;
    }

    &__contenido{
        width: 100%;
        padding: 1rem 1rem 3rem 1rem;
        overflow-y: scroll;
        @include m.tablet{
            padding: 1rem 5rem 3rem 5rem;
        }
    }

    &__contenedor--boton {
        display: flex;
        flex-direction: column;
        padding-bottom: 2rem;
    }
    
    &__boton {
        @include m.boton(v.$red-campus, v.$blanco);
        text-align: center;
        text-transform: uppercase;
        font-size: 1.4rem;
        border-radius: 20px;
    
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding: 2rem;
    
        @include m.tablet {
            width: 20rem;
            height: 10rem;
        }
    }
    
    &__contenedor--centrar {
        @include m.tablet {
            display: flex;
            flex-direction: column; // Cambiado a columna para dispositivos tipo tablet
    
            & > .row {
                display: flex;
                justify-content: space-around;
                align-items: center;
                margin-bottom: 2rem; 
            }
        }
    }

    &__contenedor-boton {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        gap: 2rem; // Añadir espacio entre los botones y el input
        margin-bottom: 5rem;
        
        @include m.tablet {
            justify-content: space-between;
        }
    }

    &__filter-input {
        padding: 0.5rem 1rem;
        border: 1px solid #ccc;
        border-radius: 5px;
        font-size: 1rem;
        transition: all 0.3s ease-in-out;
        margin-right: 40rem; // Ajusta el margen si es necesario para la separación

        &:focus {
            outline: none;
            border-color: #3498db;
            box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
        }

        &::placeholder {
            color: #aaa;
        }
    }

    &__contenedor-volver {
        @include m.tablet {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
             margin-bottom: 5rem
        }
    }
    
    &__boton-mate {
        
        @include m.boton(v.$azul, v.$blanco);
        text-transform: uppercase;
        font-size: 1.4rem;
        
        
        @include m.tablet {
            width: auto;
            
        }
    }
    
    &__contenedor{
        @include m.shadow;
        background-color: v.$blanco;
        padding: 2rem;
        border-radius: 1rem;
    }

    &__formulario{
        @extend .dashboard__contenedor;
        @include m.contenedor-md;
    }

}